@font-face {
  font-family: hummel;
  src: url(/static/media/hummel.589796c9.ttf);
}

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/static/media/background.8dc6ea29.jpg);
  font-family: Segoe UI, Roboto, Source Sans Pro, Helvetica, Arial, sans-serif;
}

a {
  text-decoration: none;
}

h2 {
  color: #fff;
}

table {
  width: 100%;
}

td span{
  float: left;
}
td p{
  float: left;
  text-align: left;
}

/* Containers */

.content-container {
  display: flex;
  flex-direction: row;
  flex-flow: wrap row;
  justify-content: space-evenly;
}

.page {
  text-align: center;
  padding: 8px 32px;
  flex: 1 1;
  background-color: #fff;
  color: white;
  background-color: rgb(71, 71, 81);
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  margin: 1em;
}

.page > h2 {
   color: #86c323;
   text-align: center;
   margin: 16px auto;
   padding: 0;
   font-size: 32px;
}

/* Icons */

.icons a {
  text-decoration: none;
  color: #5b8e4b;
}

