@media (max-width: 1000px) {
  .content-container {
    display: flex;
    flex-direction: column;
    flex-flow: column;
    justify-content: space-evenly;
  }
}

.menu-container {
    display: flex;
    justify-content: center;
    list-style: none;
    margin-top: .2em;
    -webkit-animation-name: fadeIn;
            animation-name: fadeIn;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    text-align: center;
    padding-left: 0;   
}



.menu-item a{
    flex-grow: 1;
    flex-shrink: 1;
    margin-left: 20px;
    font-size: 1.2em;
    color: #0ecaea;
    text-decoration: none;
}

.menu-item a:hover{
    flex-grow: 1;
    flex-shrink: 1;
    margin-left: 20px;
    font-size: 1.2em;
    color: #57a97b;
    text-decoration: underline;
}

@media(max-width: 1000px){
